@import url('https://fonts.googleapis.com/css2?family=Poppins:wght@300;400;500;600&display=swap');

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: 'Poppins', sans-serif;
}

body {
  height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
  background: linear-gradient(135deg, #667eea, #764ba2);
}

.form-container {
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
}

.form-box {
  width: 400px;
  background: rgba(255, 255, 255, 0.15);
  backdrop-filter: blur(15px);
  padding: 35px;
  border-radius: 15px;
  box-shadow: 0 8px 32px rgba(0, 0, 0, 0.3);
  color: #fff;
  transition: 0.3s ease;
}

h2 {
  text-align: center;
  margin-bottom: 25px;
  font-weight: 500;
}

.toggle-buttons {
  display: flex;
  justify-content: space-between;
  margin-bottom: 25px;
}

.toggle-buttons button {
  width: 48%;
  padding: 10px;
  border-radius: 8px;
  border: none;
  background: rgba(255, 255, 255, 0.2);
  color: #fff;
  cursor: pointer;
  transition: 0.3s;
}

.toggle-buttons button.active {
  background: #fff;
  color: #333;
  font-weight: 600;
}

.toggle-buttons button:hover {
  background: #fff;
  color: #333;
}

.input-group {
  display: flex;
  flex-direction: column;
  margin-bottom: 18px;
}

label {
  margin-bottom: 6px;
  font-size: 14px;
}

input {
  padding: 10px 12px;
  border-radius: 8px;
  border: none;
  outline: none;
  font-size: 14px;
  transition: 0.3s;
}

input:focus {
  box-shadow: 0 0 0 2px #fff;
}

.submit-btn {
  margin-top: 10px;
  padding: 12px;
  border-radius: 8px;
  border: none;
  background: #fff;
  color: #333;
  font-weight: 600;
  cursor: pointer;
  transition: 0.3s;
}

.submit-btn:hover {
  background: #333;
  color: #fff;
}

@media (max-width: 420px) {
  .form-box {
    width: 90%;
    padding: 25px;
  }
}
