.dashboard {
  min-height: 100vh;
  background: #f4f6fb;
  padding: 30px;
}

.dashboard h1 {
  margin-bottom: 20px;
  font-size: 28px;
  color: #333;
}

.card-container {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(220px, 1fr));
  gap: 20px;
}

.card {
  background: white;
  padding: 25px;
  border-radius: 15px;
  box-shadow: 0 8px 20px rgba(0, 0, 0, 0.05);
  transition: 0.3s;
}

.card:hover {
  transform: translateY(-5px);
  box-shadow: 0 15px 25px rgba(0, 0, 0, 0.1);
}

.card h3 {
  margin-bottom: 10px;
  color: #667eea;
}

.card p {
  color: #555;
  font-size: 14px;
}
